import {
  DateFormat,
  DateFormatEnum,
  TimeFormat,
  TimeFormatEnum
} from './types';

/**
 * Проверка параданного формата на принадлежность к типу DateFormat
 * @param {string} format строка формата
 * @returns {boolean} результат проверки
 */
export const isDateFormat = (format: string): format is DateFormat => (
  Object.values(DateFormatEnum).includes(format as DateFormatEnum)
);

/**
 * Проверка параданного формата на принадлежность к типу TimeFormat
 * @param {string} format строка формата
 * @returns {boolean} результат проверки
 */
export const isTimeFormat = (format: string): format is TimeFormat => (
  Object.values(TimeFormatEnum).includes(format as TimeFormatEnum)
);
